.layout {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  height: 100dvh;

  &__center {
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
    height: 100vh;
    height: 100dvh;

    @media (min-height: 900px) {
      justify-content: center;
    }
  }
}
