.globe-wrap {
  overflow: hidden;
  position: relative;
  flex-shrink: 0;

  border-radius: 12px;
  background: var(--map-water);
  box-shadow: 1px 2px 6px rgba(var(--black-rgb), 0.1),
    1px 2px 16px rgba(var(--black-rgb), 0.1);

  .globe {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    svg {
      position: absolute;
    }
  }

  .globe-na {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;

    font-weight: 700;
    color: var(--K500);
  }
}
